<template>
  <div class="bmi">
   <div class="container">
     <section class="main-top">
       <div class="row justify-content-center">
         <div class="col-8">
           <div class="row justify-content-between align-items-center">
               <img src="../assets/BMICLogo.png" alt="" srcset="">
               <div> 
                  <div class="form-group mb-1">
                  <label for="height" class="text-primary">身高 cm</label>
                  <input type="text" class="form-control custoom-input" id="height"  placeholder="請輸入身高" v-model.number="height">
                  </div>
                  <div class="form-group mb-1">
                  <label for="weight" class="text-primary">體重 kg</label>
                  <input type="text" class="form-control custoom-input" id="weight"  placeholder="請輸入體重" v-model.number="weight">
                </div>
                
               </div>
            <div @click="checkBMI">
              <p class="mb-0 custom-circle">看結果</p>
            </div>
           </div>
         </div>
       </div>

     </section>
     <section class="main-mid">
       <h1 class="title">BMI紀錄</h1>
       <ul class="custom-ul list-unstyled">
         <li class="bg-white custom-li" v-for="item in records" :key="item.id" :class="item.tag">
           <div class="slot-1 text-log"><span class="">{{item.slot1}}</span></div>
           <div class="slot-2">BMI <span class="large-text">{{item.slot2}}</span></div>
           <div class="slot-3">weight <span class="large-text">{{item.slot3}}kg</span></div>
           <div class="slot-4">height <span class="large-text">{{item.slot4}}cm</span></div>
           <div class="slot-5">{{item.slot5}}</div>
         </li>
         

       </ul>
     </section>
     <section class="main-bot">
       <img src="https://hexschool.github.io/JavaScript_HomeWork/assets/BMICLogo.png" alt="" srcset="">
     </section>

   </div>
  </div>
</template>
<style lang="scss" scoped>
@import "../stylesheets/all.scss";

