    display: flex;
    justify-content: space-between;
    align-items: center;
    &>*{
      min-width: 105px;
    }
  }
  .t-light{
    border-left: 7px #31BAF9 solid;
  }
  .t-normal{
    border-left: 7px #86D73F solid;
  }
  .t-over1{
    border-left: 7px #FF982D solid;
  }
  .t-over2{
    border-left: 7px #FF6C03 solid;
  }
  .t-over3{
    border-left: 7px #FF6C03 solid;
  }
  .t-over4{
    border-left: 7px #FF1200 solid;
  }
  .text-log{
    width: 80px;
    font-size: 20px;
    vertical-align: sub;
  }
  .large-text{
    font-size: 20px;
    vertical-align: sub;
    margin-left: 7px;
  }
}
.main-bot {
  padding: 18px 0;
  background-color: $primary;
  text-align: center;
}
</style>


<script>
export default {
  data() {
    return {
     records:[],
     height: '',
     weight: '',
     bmi:'',
     bmilog:'',
     tagClass:{},
    };
  },
  methods:{
    'checkBMI':function(){
      let vm =this;
      var roundDecimal = function (val, precision) {
        return Math.round(Math.round(val * Math.pow(10, (precision || 0) + 1)) / 10) / Math.pow(10, (precision || 0));
      }
      this.bmi=this.weight/((this.height/100)*(this.height/100));
      this.bmi=roundDecimal(this.bmi,2);
      switch (true) {
        case (this.bmi<18.5):
          this.bmilog='過輕'
          this.tagClass={'t-light': true}
          break;
        case (this.bmi>=18.5 && this.bmi<24):
          this.bmilog='理想'
          this.tagClass={'t-normal': true}
          break;
        case (this.bmi>=24 && this.bmi<27):
          this.bmilog='過重'
          this.tagClass={'t-over1': true}
          break;
        case (this.bmi>=27 && this.bmi<27):
          this.bmilog='輕度肥胖'
          this.tagClass={'t-over2': true}
          break;
        case (this.bmi>=30 && this.bmi<35):
          this.bmilog='中度肥胖'
          this.tagClass={'t-over3': true}
          break;
        case (this.bmi>=35):
          this.bmilog='重度肥胖'
          this.tagClass={'t-over4': true}
          break;
      
        default:
          break;
      }
      let newBmi={};
      newBmi.slot1=this.bmilog;
      newBmi.slot2=this.bmi;
      newBmi.slot3=this.weight;
      newBmi.slot4=this.height;
      let time=new Date();
      let date=time.getDate();
      let month=("0"+(time.getMonth()+1)).slice(-2);
      let year=time.getFullYear();
      newBmi.slot5=`${month}-${date}-${year}`;
      newBmi.tag=this.tagClass;
      console.log(newBmi);
      if(this.records.length==0){
        this.records.push(newBmi);
      }
      else{
        this.records.unshift(newBmi);
      }
     
      // firebase
      let fire = firebase.database().ref('bmi');
      fire.set(vm.records);
    }
  },
  mounted() {
    let vm=this;
    let fire = firebase.database().ref('bmi');
     fire.once('value',function(snapshot){
        let data = snapshot.val(); 
        if(!data){
          data=[];
        }
        vm.records=data;
        console.log(vm.records)
    })
  },
}
</script>
